import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Link, Redirect } from 'react-router-dom'

import Dialog from 'Components/dialog/dialog';
import toast from 'Components/toast/action';
import Form from 'Components/forms/Form';

const formlist = [{
    title: '用户名',
    name: 'username',
    type: 'text',
    placeholder: '请输入你的用户名'
}, {
    title: '密码',
    name: 'password',
    type: 'text',
    extype: 'password',
    placeholder: '请输入你的密码'
}, {
    title: '邮箱地址',
    name: 'email',
    type: 'text',
    extype: 'email',
    placeholder: '请输入你的邮箱地址'
}]

class Register extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            formdata: {
                username: 'tianez',
            },
            show: false,
            message: '',
        }
    }
    hanleChange = (formdata) => {
        this.setState({ formdata })
    }
    hanlePostRegister = async (from) => {
        this.setState({
            show: true,
            message: '注册中...'
        })
        let res = await request('https://d.apicloud.com/mcm/api/user', {
            method: 'post',
            data: from
        })
        this.setState({
            show: false,
            message: ''
        })
        if (res.success) {
            this.setState({
                show: true,
                message: '注册成功，正在自动登录中...'
            })
            let res = await request('https://d.apicloud.com/mcm/api/user/login', {
                method: 'post',
                data: from
            })
            this.setState({
                show: false,
                message: ''
            })
            if (res.success) {
                localStorage.authorization = res.data.id
                localStorage.userId = res.data.userId
                this.props.dispatch('user/save', { ...res.data })
                toast('登录成功')
            }
        } else {
            toast(res.message)
        }
    }
    render() {
        const { location, user } = this.props
        const from = location.from || { pathname: '/ucenter' }
        let { formdata, show, message } = this.state
        return (
            user && user.size > 0 ?
                <Redirect to={from} /> :
                <div className='warper'>
                    <div className="header">
                        <div className="h1">用户注册</div>
                    </div>
                    <div className="content">
                        <Form formdata={formdata} formlist={formlist} onChange={this.hanleChange}>
                            <div className="weui-btn-area">
                                <input type='button' onClick={() => this.hanlePostRegister(this.state.formdata)} className="weui-btn weui-btn_primary" value='确定' />
                            </div>
                            <div className="weui-btn-area">
                                <Link to={'/login'} className="weui-btn weui-btn_primary">返回登录</Link>
                            </div>
                        </Form>
                    </div>
                    <Dialog show={show}>
                        <div className="weui-toast">
                            <i className="weui-loading weui-icon_toast"></i>
                            <p className="weui-toast__content">{message}</p>
                        </div>
                    </Dialog>
                </div>
        )
    }
}

export default connect((state) => ({ user: state.get('user') }))(Register)


